.top_block_page.audit {
  .form-work {
    max-width: 298px;
    overflow: hidden;
    position: relative;
    left: unset;
    right: unset;
    top: unset;
    bottom: unset;
    border-radius: 7px;
    background-color: rgba(68, 115, 185, 0.9);
    margin-left: auto;
    transform: translateY(-25%);
