.help-block {
  background: url(../images/help-bg.jpg) no-repeat center bottom #fff;
  padding-top: 111px;
  padding-bottom: 50px;
  @media (max-width: 1200px) {
    background: none;
    padding-top: 99px;
    padding-bottom: 86px;
    .keysi__slider-icon {
      padding-top: 12px; } }
  h2 {
    &:before {
      content: '';
      display: block;
      width: 41px;
      height: 3px;
      margin: 0 auto 20px;
      background: #4473b9; }
    @include FFGB;
    color: #000000;
    font-size: 40px;
    line-height: 62px;
    text-align: center;
    margin: 0;
    padding-bottom: 4px;
    @media (max-width: 1200px) {
      padding-bottom: 32px;
      font-size: 30px;
      &:before {
        margin-bottom: 14px; } } }
  &__desc {
    @include FFGB;
    font-size: 15px;
    text-align: center;
    padding-bottom: 23px;
    @media (max-width: 1200px) {
      display: none; } }
  &__item {
    cursor: pointer;
    min-height: 50px;
    padding-left: 73px;
    font-size: 15px;
    color: #000000;
    @include FFGB;
    font-size: 15px;
    line-height: 20px;
    position: relative;
    @media (max-width: 1200px) {
      margin-bottom: 35px; }
    &:before {
      content: '?';
      border-width: 1px;
      border-color: rgb(68, 115, 185);
      border-style: solid;
      border-radius: 50%;
      background-color: rgb(255, 255, 255);
      position: absolute;
      width: 48px;
      height: 48px;
      color: #4473b9;
      font-size: 24px;
      line-height: 46px;
      text-align: center;
      @include FFGL;
      left: 0;
      top: -4px;
      @media (max-width: 1200px) {
        top: 0; } }
    &:hover {
      color: #4473b9;
      &:before {
        border-width: 1px;
        border-color: rgb(68, 115, 185);
        border-style: solid;
        border-radius: 50%;
        background-color: #4473b9;
        color: #ffffff;
        box-shadow: 0px 15px 30px 0px rgba(68, 115, 185, 0.25); } } }
  .cont {
    padding-top: 61px;
    @media (max-width: 1200px) {
      padding-top: 46px;
      display: none; } }
  .slider {
    display: none;
    @media (max-width: 1200px) {
      padding-top: 46px;
      display: block;
      margin-left: 30px;
      margin-right: 30px;
      br {
        display: none; } } }
  .row {
    margin-bottom: 53px;
    @media (max-width: 1200px) {
      margin-bottom: 0; }
    .col-sm-6:nth-last-child(1) {
      .help-block__item {
        text-align: right;
        padding-left: 0;
        padding-right: 80px;
        &:before {
          left: unset;
          right: 0; }
        @media (max-width: 1200px) {
          margin-left: 45px;
          margin-right: 45px; } } } }
  .cont {
    .row:nth-child(1),
    .row:nth-last-child(1) {
      .help-block__item {
        margin-left: 65px;
        margin-right: 65px;
        @media (max-width: 1200px) {
          margin-left: 45px;
          margin-right: 45px; } } }
    .row:nth-child(2),
    .row:nth-last-child(2) {
      .help-block__item {
        margin-left: 16px;
        margin-right: 16px;
        @media (max-width: 1200px) {
          margin-left: 45px;
          margin-right: 45px; } } }
    .row:nth-child(3) {
      .help-block__item {
        margin-left: -10px;
        margin-right: -10px;
        @media (max-width: 1200px) {
          margin-left: 45px;
          margin-right: 45px; } } } } }

